.input {
  border: none;
  border-radius: 5px;
  height: 20px;
  margin: 0 auto;
  margin-bottom: 10px;

  display: block;
  padding: 14px;
}

.button {
  text-decoration: none;
  display: block;
  padding: 10px 20px;
  overflow: hidden;
  border: none;
  border-radius: 5px;
  background-color: rgba(27, 27, 27, 0.164);
  transition: 0.2s ease-in-out;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 30px;
  cursor: pointer;
}

.button:hover {
  background: rgba(33, 17, 177, 0.658);
  color: #fff;
}

.form {
  margin: 0 auto;
}
